
































































@import "@/assets/scss/variables.scss";

#sidebar {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 29px;
  right: 24px;

  button {
    margin: 4px;
    i.UI-icon.UI-plus {
      top: -3px;
    }
    i.UI-icon {
      left: 1px;
    }
    &[name="new-column"] {
      display: none;
      @include breakpoint(mobile) {
        display: block;
      }
    }
  }
}

.appear-enter, .appear-leave-to {
  opacity: 0;
  transform: scale(0);
}
.appear-enter-active, .appear-leave-active {
  transition: transform 0.2s;
}
.appear-enter-to, .appear-leave {
  opacity: 1;
  transform: scale(1);
}

@media print {
  #sidebar {
    display: none;
  }
}
