




























































































































































































































@import "@/assets/scss/variables.scss";
$duration: 500ms;
.vue-recyclist {
  overflow-x: hidden;
  overflow-y: auto;
  overscroll-behavior-y: contain;
  contain: strict;
  -webkit-overflow-scrolling: touch;

  .vue-recyclist-items {
    position: relative;
    .vue-recyclist-item {
      position: absolute;
      width: 100%;
    }
    .vue-recyclist-pool {
      .vue-recyclist-item {
        visibility: hidden;
      }
    }
  }
  .vue-recyclist-nomore {
    overflow: hidden;
    margin: 10px auto;
    height: 20px;
    text-align: center;
  }
  /* width */
  &::-webkit-scrollbar {
    width: 0.3em;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: transparent;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background-color: darken($background-color, 5);
    border-radius: 4px;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: darken($background-color, 15);
  }
}
.night-mode {
  .vue-recyclist {
    &::-webkit-scrollbar-thumb {
      background-color: darken($background-color-night, 5);
    }
    &::-webkit-scrollbar-thumb:hover {
      background: darken($background-color-night, 15);
    }
    .vue-recyclist-nomore {
      color: $grey-cold-5;
    }
  }
}
